import { Link, useNavigate } from "react-router-dom";
import "./form.css";
import { useState } from "react";
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import { registerUser } from "../../redux/apiCalls/authApiCall";
import swal from "sweetalert";

const Register = () => {
  const dispatch = useDispatch();
  const { registerMessage } = useSelector((state) => state.auth);

  const [username, setUsername] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  // Form Submit Handler
  const formSubmitHandler = (e) => {
    e.preventDefault();
    if (username.trim() === "") return toast.error("Dane s wymagane");
    if (email.trim() === "") return toast.error("Email jest wymagany");
    if (password.trim() === "") return toast.error("Haso jest wymagane");

    dispatch(registerUser({ username, email, password }));
  };

  const navigate = useNavigate();

  if (registerMessage) {
    swal({
      title: registerMessage,
      icon: "success",
    }).then((isOK) => {
      if (isOK) {
        // go to login page
        navigate("/login");
      }
    });
  }

  return (
    <section className="form-container">
      <h2 className="form-title">Utwrz nowe konto</h2>
      <form onSubmit={formSubmitHandler} className="form">
        <div className="form-group">
          <label htmlFor="username" className="form-label">
            Uytkownik
          </label>
          <input
            type="text"
            className="form-input"
            id="username"
            placeholder="Wpisz swoje imi i nazwisko lub Nick"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="email" className="form-label">
            Email
          </label>
          <input
            type="email"
            className="form-input"
            id="email"
            placeholder="Wpisz swj email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-group">
          <label htmlFor="password" className="form-label">
            Haso
          </label>
          <input
            type="password"
            className="form-input"
            id="password"
            placeholder="Wpisz swoje haso"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <button className="form-btn" type="submit">
          Zarejestruj si
        </button>
      </form>
      <div className="form-footer">
        Masz ju konto? <Link to="/login">Zaloguj si</Link>
      </div>
    </section>
  );
};

export default Register;
